.app__specialMenu {
    flex-direction: column;
    background: var(--color-black);
}

.app__specialMenu-title {
    margin-bottom: 2rem;
    text-align: center;
}



.app__specialMenu-menu_img {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: row;
  }
  
  .app__specialMenu-menu_img .image-container {
    position: relative;
    margin: 1rem 1rem;
    width: 25rem;
    height: 15rem;
    overflow: hidden;
    cursor: pointer;
  }
  
  .app__specialMenu-menu_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: .5s ease;
    
  }

.image-container:hover img{
    transform: scale(1.2);
    
}
  
  .app__specialMenu-menu_img .text-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    background-color: rgba(0, 0, 0, 0.7);
    align-items: center;
    justify-content: center;
  }
  
  .app__specialMenu-menu_img .image-container:hover .text-overlay {
    display: flex;
  }


  
  .app__specialMenu-menu_img .text-overlay p {
    color: #fff;
    font-size: 25px;
    text-align: center;
  }

.app__specialMenu-menu_img img {
    margin: 1rem 1rem;
    width: 25rem;
    height: 15rem;
   
}



@media screen and (min-width: 2000px) {
    .app__specialMenu-menu_img {
        width: 650px;
    }

    .app__specialMenu-menu_img img {
        height: 920px;
    }
}

@media screen and (max-width: 1150px) {

    .app__specialMenu-menu_img {
        margin: 3rem 0;
    }
}

@media screen and (max-width: 650px) {
    .app__specialMenu-menu_img {
        width: 100%;
    }
}