.app__header {
    background-color: var(--color-black);
    height: 100vh;
}
.app__header-h1{
    font-family: var(--font-base);
    color: var(--color-blue);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    line-height: 117px;
    font-size:90px;
    font-weight: 700;  
}

.app__wrapper_img img{
    margin-bottom: 4rem;
    border: 3px solid var(--color-blue);   
    transform: scale(1.3);
}